
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'

import {mapActions} from "vuex" ;
export default {
name:"msgs",
data(){
return{
selected: 0 ,//默认选中未读
hasReader:"",
hasNotReadsr:"",
mak_id:""
}
},
components: {
ButtonTab,
ButtonTabItem,
Cell,
Group,
Swipeout,
SwipeoutItem,
SwipeoutButton
},
methods:{
...mapActions(["getMsgsCount"]),
getReadMsgs(){
var app = this;
this.$http.get("/messages",{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data);
if (res.data && res.data.success) {
app.hasReadMsgs = res.data.data.has_read_messages;
app.hasNotReadMsgs = res.data.data.hasnot_read_messages;
}
})
},
handleTab(index){
this.selected = index;
},

makRead(){
var app = this;
this.$http.post("/message/mark_one/" + mak_id,{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
if (res.data && res.data.success) {
app.getgetMsgsCount();
console.log("标记已读成功");
var markMsgs;
for (var i = 0; i < app.hasNotReadsr.length; i++) {
if (app.hasNotReadsr[i].id == mak_id) {
markMsgs = app.hasNotReadsr[i];
app.hasNotReadsr.splice(i,1);
break;
}
}
app.hasReader.unshift(markMsgs);
}
})
},
makAll(){
this.$http.post("/message/mark_all",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
console.log(res.data);
if (res.data && res.data.success) {
app.$router.go(0);
}
})
}
},
created(){
this.getReadMsgs();
}
}



