import Vue from 'vue'
import VueRouter from 'vue-router';
import Home from "../components/Home.vue";
import TopicList from "../components/TopicList";
import Topic from "../components/Topic";
import Center from "../components/Center.vue";
import Msgs from "../components/Msgs.vue";
import Login from "../components/Login.vue";
import Logout from "../components/Logout";
import User from "../components/User.vue";
import Create from "../components/Create.vue";
import Edit from "../components/Edit.vue";

Vue.use(VueRouter);

const routes = [
  // {
  //   path:"/",
  //   component:Home
  // },
  //主体列表(name)
  //重定向列表，默认加载主体列表的全部分类
  {
    path:"/center",
    component:Center
  },
  {
    path:"/msgs",
    component:Msgs
  },
  {
    path:"/login",
    component:Login
  },
  {
    path:"/logout",
    component:Logout
  },
  {
    path:"/",
    redirect:"/topislist/all"
  },
  {
    path:"/topiclist/:name",
    name:"topiclist",
    component:TopicList,
    props:true
  },
  //主体详情(id)
  {
    path:"/topic/:id",
    name:"topic",
    component:Topic,
    props:true
  },
  {
    path:"/user/:loginname",
    name:"user",
    component:User,
    props:true
  },
  {
    path:"/create",
    name:"create",
    component:Create,
    props:true
    },
  {
    path:"/topic/:id/edit",
    name:"edit",
    component:Edit,
    props:true,
  }
];

const router = new VueRouter({
  routes
})

export default router;




