
import CellBox from 'vux/src/components/cell-box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import {mapState} from "vuex";
export default {
name:"topic",
props:['id'],
data(){
return{
topics:"",   //帖子详情
reply_content:"",
reply_content2:"",
isShowPopup:false,
reply_id:null,
reply_loginname:null,
}
},
components:{
CellBox,
Group,
Flexbox,
FlexboxItem ,
XTextarea,
XButton,
Card,
Popup
},
directives:{
TransferDom
},
computed:{
...mapState(['islogined'])
},
methods:{



deCollect(){
var app = this;
this.$http.post("/topic_collect/de_collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.topics.is_collect = false;
}
})
},




handelPopup(reply_id,reply_name){
this.isShowPopup=true;
this.reply_id = reply_id;
this.reply_content2 = "@"+reply_name+" " +this.reply_content2
},




collect(){
var app = this;
this.$http.post("/topic_collect/collect",{
accesstoken:this.$store.state.accesstoken,
topic_id:this.topics.id
}).then(function(res){
console.log(res.data)
if(res.data.success){
app.topics.is_collect = true;
}
})
},


addReply(replyType){

var app = this;

var params = {
accesstoken:this.$store.state.accesstoken,
content:this.reply_content
}

if(replyType == 2){
params.reply_id = this.reply_id;
params.content = this.reply_content;
}


this.$http.post("/topic/"+this.topics.id+"/replies",params).then(function(res){
console.log(res);
if(res.data.success){//评论成功，手动刷新加载评论内容
var reply={
"id": res.data.reply_id,
"author": {
"loginname":app.$store.state.loginname,
"avatar_url": "https://avatars.githubusercontent.com/u/84430504?v=4&s=120"
},
"content": params.content,
"ups":[],
"create_at": "2021-05-27T00:55:19.433Z",
"reply_id":null,
"is_collect": false
}
app.topics.replies.push(reply);

app.reply_content="";
app.reply_content2="";
app.isShowPopup=false;
app.reply_id=null;

}
})
},



upOrDown(reply_id){
this.$http.post("/reply/"+ reply_id +"/ups",{
accesstoken:this.$store.state.accesstoken
}).then(function(res){
if(res.data.success){
var reply;
for (var i = 0; i < app.topics.replies.length; i++) {
if (app.topics.replies[i].id == reply_id) {
reply = app.topics.replies[i];
break;
}
}
if (res.data.action == "up") {

reply.ups.push(app.$store.state.author_id);
}
if (res.data.action == "down") {

var index =   reply.ups.indexOf(app.$store.state.author_id);
console.log(index);
reply.ups.splice(index,1);
}
}
})

}
},


beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:vm.$store.state.accesstoken
}
}).then(function(res){
console.log(res.data);
if(res.data.success){
vm.topics = res.data.data;
}
})
})
},


beforeRouteUpdate(to,from,next){
var app = this;
this.$http.get("/topic/"+to.params.id,{
params:{
accesstoken:this.$store.state.accesstoken
}
}).then(function(res){
if(res.data.success){
app.topics = res.data.data;
}
})
next();
}
}



