
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import { mapState,mapGetters} from "vuex";
export default {
name:"topiclist",
props:['name'],
data(){
return{
listdata:[],
page:1,  //页码
limit:15,  //每页显示数据
pullupConfig:{
content:'上拉加载更多',
downContent:'松开进行加载',
upContent:'上拉加载更多',
loadingContent:'加载中...'
},
pulldownConfig:{
content:'下拉加载更多',
downContent:'松开进行加载',
upContent:'下拉加载更多',
loadingContent:'加载中...'
},
}
},
computed:{
...mapState(['topiclist']),
...mapGetters(["fileterTab"])
},
components:{
Cell,
Group,
Scroller,
Tab,
TabItem
},
methods:{
loadMore(){
var app = this;
this.page = this.page + 1;
this.$http.get("/topics",
{
params:{
tab:this.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
for(var i = 0;i<response.data.data.length;i++){
app.listdata.push(response.data.data[i]);
}

app.$refs.pullup.donePullup();
}
})
},
handelTab(index){
console.log(index);




this.$router.push("/topiclist/"+this.topiclist[index].name)
},
refreshData(){
var app = this;
this.$http.get("/topics",{
params:{
tab:this.name,
page:1,
limit:this.limit
}
}).then(function(res){
if(res.data.success){
app.listdata = res.data.data;
app.$refs.pullup.donePulldown();
app.page = 1;
}
})
}
},



beforeRouteEnter(to,from,next){
next(function(vm){
vm.$http.get("/topics",
{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit
}
}).then(function(response){
console.log(response);
vm.listdata = response.data;
if(response.data.success){
vm.listdata = response.data.data;
}
})
})
},     //动态路由被切换之前被调用
beforeRouteUpdate(to,from,next){
var app = this
this.$http.get("/topics",
{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit
}
}).then(function(response){
console.log(response);
if(response.data.success){
app.listdata = response.data.data;
}
})

next();
},

}



