import Vue from "vue";
import Vuex from "vuex";
import mutations from "./mutations.js";
import actions from "./actions.js";

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        topiclist:[    //导航
            {name:"all",desc:"全部"},
            {name:"good",desc:"精华"},
            {name:"share",desc:"分享"},
            {name:"ask",desc:"问答"},
            {name:"jod",desc:"招聘"},
            {name:"dev",desc:"测试"}
          ],
           isShow:false,   //默认不显示actions菜单
           islogined:false,  //默认未登录状态
           loginedMenus:{
           create:"发表新话题",
           center:"个人中心",
           msgs:"消息中心",
           logout:"退出"
          } ,
          unloginMenus:{
          login:"登录"
          },
          loginname:"", //保存登录后的用户名
          author_id:"", 
          accesstoken:"",  //保存访问令牌
          msgsCount:"" 
    },
    getters:{
        //判断用户登录前后的状态
        mormenus(state){
            if(state.islogined){    //判断用户是否登录
              return state.loginedMenus  //登录后
            }
            return state.unloginMenus      //未登录
              },
        //处理分类显示汉字
        fileterTab(state){
           return function(tab){
             for (let i = 0; i < state.topiclist.length; i++) {
               const element = state.topiclist[i];
               if (element.name == tab) {
                 return state.topiclist[i].desc;
               }
             }
           }
        }
    },
    mutations:mutations,
    actions:actions,
})
export default store;


