
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import { mapGetters,mapMutations,mapState,mapActions} from "vuex";
import VmBackTop from "vue-multiple-back-top";
export default {
name: 'app',
data(){
return{

}
},
computed:{
...mapGetters(["mormenus"]),
...mapState(["msgsCount"]),
isShow:{
get(){return this.$store.state.isShow;},
set(nval){return this.$store.commit('mutationsIsshow',nval);}
},
},
components: {XHeader,VmBackTop,Actionsheet,Badge },
methods:{

handelTile(){
this.$router.push("/topiclist/all")
} ,
showMenus(){this.isShow = true;},
handelActions(menuKey){ this.$router.push("/"+menuKey);},

...mapMutations(['mutationsLogined',"mutationsToken","mutationsLoginname","mutationsAuthor"]),
...mapActions(["getMsgsCount"]),




},
created(){

var loginStatus = window.localStorage.getItem("loginStatus");

if(loginStatus){
this.mutationsLogined();
this.mutationsLoginname(window.localStorage.getItem("loginname"));
this.mutationsToken(window.localStorage.getItem("accesstoken"));
this.mutationsAuthor(window.localStorage.getItem("author_id"));


this.getMsgsCount();
var app = this;
window.setInterval(function(){
app.getMsgsCount();
},1000*60*5)
}
}

}



